<template>
  <div>
    <h3>게시판 글쓰기</h3>
    <hr />

    <input type="text" v-model="state.btitle" placeholder="제목" /><br />
    <input type="text" v-model="state.bcontent" placeholder="내용" />
    <button @click="handleInsert">글쓰기</button>
  </div>
</template>

<script>
import { reactive } from "@vue/reactivity";
import { useRouter } from "vue-router";
import axios from "axios";
export default {
  setup() {
    const router = useRouter();
    const state = reactive({
      btitle: "",
      bcontent: "",
    });

    const handleInsert = async () => {
      const url = `/ROOT/api/board/insert`;
      const headers = { "Content-Type": "application/json" };
      const body = {
        btitle: state.btitle,
        bcontent: state.bcontent,
      };
      const response = await axios.post(url, body, headers);
      console.log(response.data);
      if (response.data.status === 200) {
        alert("등록이 완료되었습니다.");
        router.push({ name: "Board" });
      }
    };
    return { handleInsert, state };
  },
};
</script>

